<template lang="html">
  <x-header>
    首页
    <a slot="right" @click="update" href="javascript:;">
      保存
    </a>
  </x-header>
  <div class="team_logo">
    <div class="logo">
      <img :src="user.avatar.avatar.url" alt="" />
      <input type="file" accept="image/jpg,image/jpeg,image/png,image/gif" multiple />
    </div>
  </div>
  <div class="weui_cells weui_cells_form">
    <div class="weui_cell">
      <div class="weui_cell_hd" style="width:5%;">
        <label class="weui_label">
          <i class="fa fa-user"></i>
        </label>
      </div>
      <div class="weui_cell_bd weui_cell_primary">
        <input class="weui_input" type="text" placeholder="名称" v-model="user.name"/>
      </div>
    </div>
    <div class="weui_cell">
      <div class="weui_cell_hd" style="width:5%;">
        <label class="weui_label">
          <i class="fa fa-h-square"></i>
        </label>
      </div>
      <div class="weui_cell_bd weui_cell_primary">
        <input class="weui_input" type="text" placeholder="身高" v-model="user.height"/>
      </div>
    </div>
    <div class="weui_cell">
      <div class="weui_cell_hd" style="width:5%;">
        <label class="weui_label">
          <i class="fa fa-wordpress"></i>
        </label>
      </div>
      <div class="weui_cell_bd weui_cell_primary">
        <input class="weui_input" type="text" placeholder="体重" v-model="user.weight"/>
      </div>
    </div>
  </div>
  <toast :show.sync="loadFail" type="text">加载失败</toast>
</template>

<script>
  import XHeader from 'vux/dist/components/x-header'
  import Toast from 'vux/dist/components/toast'

  export default {
    data: function () {
      return {
        user: {
          avatar: {avatar: {url: ''}}
        },
        loadFail: false
      }
    },
    route: {
      data (transition) {
        let userId = localStorage.getItem('ruzun_user_id') || 0
        this.$http.get(this.$config.API + '/users/' + userId + '.json').then(function (response) {
          if (typeof response.data === 'string') {
            response.data = JSON.parse(response.data)
          }
          this.$set('user', response.data.user)
        }, function (response) {
          this.$set('loadFail', true)
        })
      }
    },
    methods: {
      update () {}
    },
    components: {
      XHeader,
      Toast
    }
  }
</script>

<style lang="css">
</style>



